import React, { Component } from "react";

import Dialog from "material-ui/Dialog";
import FlatButton from "material-ui/FlatButton";
import { Tabs, Tab } from "material-ui/Tabs";
import Snackbar from "material-ui/Snackbar";


import PresetPage from "./PresetPage";
import { tab1Name, tab2Name, preset1Buttons, preset2Buttons, pollingInterval } from "./config";

import { lpc } from "./MockLPC";

const layout = {
  height: "100%",
  padding: 0,
  margin: 0,
  display: "flex",
  alignItems: "center",
  justifyContent: "center"
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activePreset: -1,
      playPreset: {},
      dialogOpen: false,
      snack: {
        snackOpen: false,
        snackMessage: "",
        isError: false
      }
    };
  }

  componentDidMount() {
    this.setActivatePreset();
    setInterval(this.setActivatePreset, pollingInterval * 1000);
  }

  setActivatePreset = () => {
    lpc.getActivePreset()
      .then((number) => {
        this.setState({ activePreset: number });
      })
      .catch(() => {
        console.error("Could not get active preset from LPC");
      });
  }

  handleActivatePreset = (number, name) => {
    const presetObj = { number, name };

    this.setState({
      playPreset: presetObj,
      dialogOpen: true
    });
  }

  handleSnackClose = () => {
    this.setState({
      snack: {
        snackOpen: false,
        snackMessage: "",
        isError: false
      }
    });
  }

  handleClose = shouldPlay => () => {
    console.log("in close");
    this.setState({
      dialogOpen: false
    });

    if (!shouldPlay) {
      console.log("not playing");
      return;
    }

    const presetToPlay = this.state.playPreset.number;
    const presetToPlayName = this.state.playPreset.name;
    lpc.activatePreset(presetToPlay)
      .then(() => {
        this.setState({
          activePreset: presetToPlay,
          snack: {
            snackOpen: true,
            snackMessage: `Preset ${presetToPlayName} activated`,
            isError: false
          }
        });
      })
      .catch(() => {
        console.error(`Could not active preset: ${presetToPlay}`);
        this.setState({
          snack: {
            snackOpen: true,
            snackMessage: `Error while trying to activate preset:  ${presetToPlayName}`,
            isError: true
          }
        });
      });
  }

  render() {
    const actions = [
      <FlatButton
        label="Cancel"
        primary={true}
        onClick={this.handleClose(false)}
      />,
      <FlatButton
        label="Play"
        primary={true}
        keyboardFocused={true}
        onClick={this.handleClose(true)}
      />];
    const {snackOpen, snackMessage, isError} = this.state.snack;
    const snackStyle = isError ? {color: "red"} : {}
    return (
      <div>

        <Snackbar
          open={snackOpen}
          message={snackMessage}
          autoHideDuration={4000}
          onRequestClose={this.handleSnackClose}
          contentStyle={snackStyle}
        />

        <Dialog
            title="Preset Confirmation"
            actions={actions}
            modal={false}
            open={this.state.dialogOpen}
            onRequestClose={this.handleClose(false)}
          >
          Are you sure you want to play preset "{this.state.playPreset.name}"?
        </Dialog>

        <Tabs>
          <Tab label={tab1Name}>
            <div style={layout}>
              <PresetPage 
                config={preset1Buttons} 
                activatePreset={this.handleActivatePreset}
                activePreset={this.state.activePreset}/>
            </div>
          </Tab>
          <Tab label={tab2Name} >
            <div style={layout}>
              <PresetPage 
                config={preset2Buttons}
                activatePreset={this.handleActivatePreset}
                activePreset={this.state.activePreset}/>
            </div>
          </Tab>
        </Tabs>
      </div>
    );
  }
}

export default App;



