import React, { Component } from "react";

import PresetButton from "./PresetButton";

const layout = {
  display: "flex",
  flexWrap: "wrap",
  maxWidth: 600,
  minWidth: 380,
  textAlign: "center",
  alignItems: "flex-start",
};

const buttonStyle = {
  margin: 5,
  width: "30%"
};

class PresetPage extends Component {
  constructor(props) {
    super(props);
    const buttonStates = new Map();
    this.props.config.forEach((entry) => {
      buttonStates.set(entry.presetNumber, false);
    });
  }

  buildPresetButtons() {
    const buttonArray = [];
    this.props.config.forEach((entry) => {
      const buttonState = this.props.activePreset === entry.presetNumber;
      buttonArray.push(<PresetButton
        key={entry.presetNumber}
        presetNumber={entry.presetNumber}
        displayName={entry.displayName}
        style={buttonStyle}
        isActive={buttonState}
        activate={this.props.activatePreset}
        backgroundColor={entry.backgroundColor}
      />);
    });
    return buttonArray;
  }

  render() {
    const buttons = this.buildPresetButtons();
    return (
      <div style={layout}>
        {buttons}
      </div>
    );
  }
}

export default PresetPage;



