
export const pollingInterval = 20;

export const tab1Name = "Presets 1";

const testGen = (basename) => {
  const array = [];
  for (let i = 1; i < 50; i++) {
    array.push({ displayName: `Button ${i}`, presetNumber: i });
  }
  return array;
};

export const preset1Buttons = testGen();

