import React, { Component } from "react";

import RaisedButton from "material-ui/RaisedButton";

class PresetButton extends Component {

  constructor(props) {
      super(props);
      this.state = {
          isActive: this.props.isActive
      }
  }  

  handleClick = () => {
    const {presetNumber, displayName, activate} = this.props;
    activate(presetNumber, displayName);
  }

  render() {
    const isActive =  this.props.isActive;
    const {presetNumber, displayName, style, backgroundColor} = this.props;
    return (
        <RaisedButton key={presetNumber}
            label={displayName}
            style={style}
            primary={isActive}
            onClick={this.handleClick} 
            backgroundColor={backgroundColor}/>
    );
  }
}

export default PresetButton;



