/* eslint-disable */
class MockLpc {
  constructor() {
    this.activePreset = 1;
  }

  activatePreset(number) {
    this.activePreset = number;
    return new Promise((resolve, reject) => {
      number === 2 ? reject() : resolve();
    });
  }

  getActivePreset() {
      return new Promise((resolve, reject) => {
          console.log(this.activePreset);
          resolve(this.activePreset);
          // comment out the above line and uncomment this one
          // if you want to get random presets back from this call
          /*
          const randomPreset = Math.random() * 10;
          console.log(randomPreset);
          resolve(parseInt(randomPreset, 10));
          */
  });
  }
}

export const lpc = new MockLpc();



